@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.footer-container {
  display: flex;
  margin: 0px 25px;
  align-items: center;
  flex-direction: column;
  padding: 10px 0;
  color: #727272;
  justify-content: space-between;
  font-weight: 700;
}

.header-container {
  display: flex;
  height: 70px;
  justify-content: space-between;
  align-items: center;
  padding: 0 4px;
  position: fixed;
  width: 100%;
  z-index: 3;
  transition: all ease 0.45s;
}

.header-container:hover { 
  background-color: #181818;
}

.netflix-logo, .user-icon {
  border-radius: 4px;
  margin: 10px;
  cursor: pointer;
}

.user-icon {
  height: 40px;
}

.netflix-logo {
  height: 90px;
}

.main-movie {
  height: 100vh;
}

.vertical-shadow {
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, #111 5%, transparent 95%);
}

.horizontal-shadow {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 30px;
  padding-bottom: 170px;
  padding-top: 105px;
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 20%, transparent 80%);
}

.main-movie-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
}

.main-movie-name {
  font-size: 60px;
  font-weight: 700;
  padding-top: 15px;
}

.main-movie-infos {
  display: flex;
  font-size: 18px;
  font-weight: 700;
  margin: 15px 0;
  width: 25vh;
  justify-content: space-around;
  flex-wrap: wrap;
}

.main-movie-points {
  color:  #46d369; 
  padding: 3px;
}

.main-movie-description {
  font-size: 20px;
  color: #999;
  max-width: 50%;
}

.main-movie-buttons {
  margin-top: 15px;
  display: flex;
  max-width: 55vh;
  font-weight: 700;
  justify-content: space-around;
}

.main-movie-buttons a {
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  opacity: 1;
  transition: all ease 0.2s;
}

.main-movie-buttons a:hover {
  opacity: 0.6;
}

.main-movie-year {
  background: rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(63, 59, 59);
  border-radius: 5px;
  padding: 3px;
}



.watch-button {
  background-color: #FFF;
  color: #000;
}

.my-list-button {
  background-color: #333;
  color: #FFF;
}

.main-movie-genres {
  margin-top: 15px;
  font-size: 18px;
  color: #999;
}


@media screen and (max-width : 960px) {

  .main-movie-name {
    font-size: calc(90% + 5.5vmin);
  }

  .main-movie-infos {
    width: 30vh;
    flex-wrap: nowrap;
  }

  .main-movie-description {
    font-size: 19px;
    max-width: 75%;
  }
}

@media screen and (max-height : 400px) {


  .main-movie-name {
    padding: 0;
  }


  .main-movie-buttons {
    max-width: calc(40% + 8vmin);
    position: relative;
    left: 350px;
  }

  .name-and-infos-container {
    display: flex;
    margin-top: 100px;
    justify-content: space-between;
    width: 65%;

  }

  .main-movie-infos {
    align-content: center;
    width: 100%;
  }

}


.movie-list-container {
  display: flex;
  flex-wrap: wrap;
}

.loading {
  position: fixed;
  overflow: hidden;
  width: 100%;
}

.is-loading {
  background-color: rgb(0, 0, 0);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
}

.movie-list {
  display: flex;
  flex-wrap: wrap;
  margin-top: -105px;
  margin-bottom: 105px;
  overflow: hidden;
}

.movie-cards-container {
  display: flex;
  padding: 4px 25px;
  transition: all ease 1s;
  overflow: hidden;
}

.movie-cover {
  transform: scale(0.92);
  width: 200px;
  height: 300px;
  border-radius: 5px;
  transition: all ease 0.2s;
}

.movie-cover:hover {
  transform: scale(1);
}

.movie-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  cursor: pointer;
}

.list-title {
  margin-left: 30px;
}

.arrow-left, .arrow-right {
  display: flex;
  position: absolute;
  align-items: center;
  width: 40px;
  height: 300px;
  z-index: 2;
  cursor: pointer;
  opacity: 0;
  overflow: hidden;
  transition: all ease 0.5s;
}

.arrow-left {
  left: 0px;
}

.arrow-right {
  right: 0px;
}

.movie-list:hover .arrow-left {
  background: linear-gradient(to right, #171717, transparent);
  opacity: 1;
}

.movie-list:hover .arrow-right {
  background: linear-gradient(to left, #171717, transparent);
  opacity: 1;
}

@media screen and ( max-width : 900px) {
  .arrow-left, .arrow-right {
    opacity: 1;
  }
}

* {
  box-sizing: border-box;
}

body {
  background-color: #111;
  color: white;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}
